options loaded from the video tag here to autoplay. -->\n        <video-js id=\"main-player\"\n                  class=\"video-js vjs-big-play-centered vjs-fit\"\n                  autoplay\n                  muted\n                  playsinline\n        >\n            <source :type=\"$page.props.firstPlayVideoSourceType\" :src=\"$page.props.firstPlayVideoSource\">\n<!--            <source type=\"video/youtube\" src=\"https://www.youtube.com/watch?v=fqaHrwOhihI\">-->\n<!--            <source type=\"video/youtube\" src=\"https://www.youtube.com/watch?v=xjS6SftYQaQ&list=SPA60DCEB33156E51F\">-->\n        </video-js>\n    </div>\n\n</template>\n\n\n<script setup>\nimport { useVideoPlayerStore } from \"@/Stores/VideoPlayerStore\"\nimport { useStreamStore } from \"@/Stores/StreamStore\"\nimport { useChatStore } from \"@/Stores/ChatStore\"\nimport { useUserStore } from \"@/Stores/UserStore\"\nimport { onMounted } from \"vue\"\nimport videojs from \"video.js\"\nimport youtube from \"videojs-youtube\"\n\nlet videoPlayerStore = useVideoPlayerStore()\nlet streamStore = useStreamStore()\nlet chatStore = useChatStore()\nlet userStore = useUserStore()\n\nonMounted( () => {\n    let videoPlayer = videojs('main-player', {\n        controls: false,\n        muted: false,\n        autoplay: true,\n        preload: 'auto',\n        techOrder: [\"html5\", \"youtube\"],\n        youtube: {\"customVars\":\n                    {\n                    \"wmode\": \"transparent\"\n                    }}\n    })\n    // let videoPlayer = videojs('main-player')\n    videoPlayer.ready(function() {\n        videoPlayer.muted(true)\n        videoPlayerStore.muted = true\n        videoPlayer.play();\n    });\n})\n\n// async function getFirstPlaySettings() {\n//     await axios.get('/api/app_settings')\n//         .then(response => {\n//             videoPlayerStore.videoSource = response.data[0].first_play_video_source\n//             videoPlayerStore.videoSourceType = response.data[0].first_play_video_source_type\n//             videoPlayerStore.videoName = response.data[0].first_play_video_name\n//             console.log('app settings retrieved.');\n//\n//         })\n//         .catch(error => {\n//             console.log(error)\n//         })\n//     // setVideoOptions()\n//     // videoJs = videojs('main-player', videoOptions)\n// }\n\n</script>\n\n<style>\n\n.video-js .vjs-tech {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n}\n\n.video-js {\n    /*display: block;*/\n    /*vertical-align: top;*/\n    /*box-sizing: border-box;*/\n    color: #fff;\n    background-color: #000;\n    //position: relative;\n    padding: 0;\n    font-size: 10px;\n    line-height: 1;\n    font-weight: normal;\n    font-style: normal;\n    font-family: Arial, Helvetica, sans-serif;\n    word-break: initial;\n}\n.video-js:-moz-full-screen {\n    position: absolute;\n}\n.video-js:-webkit-full-screen {\n    width: 100% !important;\n    height: 100% !important;\n}\n\n.video-js[tabindex=\"-1\"] {\n    outline: none;\n}\n\n.video-js *,\n.video-js *:before,\n.video-js *:after {\n    box-sizing: inherit;\n}\n\n\n\n.vjs-default-skin .vjs-resume-modal .vjs-resume-modal-buttons {\n    position: relative;\n    top: 50%;\n    transform: translateY(-50%);\n    background-color: #111;\n    color: #fff;\n    margin: 0 auto;\n    padding: 10px 0;\n    font-size: 14px;\n    width: 225px;\n    border-radius: 10px;\n    text-align: center;\n}\n\n.vjs-default-skin .vjs-resume-modal .vjs-resume-modal-buttons p {\n    margin: 0 0 10px 0;\n}\n\n.vjs-default-skin .vjs-resume-modal .vjs-resume-modal-buttons button {\n    color: #fff;\n    padding: 10px;\n    background-color: #42a5f5;\n    border-radius: 5px;\n    margin: 5px;\n    font-size: 12px;\n    cursor: pointer;\n}\n\n.vjs-default-skin .vjs-resume-modal .vjs-resume-modal-buttons button:hover {\n    background-color: #90caf9;\n}\n\n.videoPlayer {\n    width:10rem;\n    height:10rem;\n    top:0px;\n    